
import {IconButton} from "@material-ui/core";
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';

const Items=(props)=>{
  const{elem,Done,editItem,deleteItem}=props;
  return(<>
  <div className="eachItem" key={elem.id}>
    <IconButton color="primary" className="fa-Line" onClick={()=>Done(elem.id)}>
      {elem.complete?<VisibilityOffIcon/>:<VisibilityIcon/>}
    </IconButton>
    <h3 className={elem.complete?"Comp":""}>{elem.name}</h3>
    <div>
      <IconButton color="primary" className=" fa-edit" onClick={()=>editItem(elem.id)}>
        <CreateIcon/>
      </IconButton>
      <IconButton color="secondary" className="fa-trash-alt" onClick={()=>deleteItem(elem.id)}>
        <DeleteIcon/>
      </IconButton>
    </div>
  </div>            
  </>)
}
export default Items;