import React ,{useEffect,useState}from "react";
import "./weather.scss";
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import PinDropIcon from "@material-ui/icons/PinDrop"
import SearchIcon from "@material-ui/icons/Search"
import {IconButton,TextField} from "@material-ui/core"
const theme = createMuiTheme({
  overrides: {
      MuiInput:{
        input:{
          color:'rgb(255,255,255)',
        },
        underline:{
          width:'100%',
          
        }
      },
    // Style sheet name ⚛️
      MuiTextField: {
       root: {
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius:5,
        border: 0,
        width:'70%',
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    },
   },
  },
});




const WeatherApp=()=>{
  const [City,setCity]=useState("");
  const [Res,setRes]=useState("");
  useEffect(()=>{
    const fetching=async()=>{
    const url=`https://api.openweathermap.org/data/2.5/weather?q=${City}&units=metric&appid=5e7750a7936396672f0a0f69b1a4370e`;
    const response=await fetch(url)
    const data=await response.json();
    return setRes(data);
    }
    fetching();
    
  },[City]);
  const GetCity=(e)=>{
    console.log(City)
    e.preventDefault();
    setCity(e.target[0].value);
  }
  return(
    <>
      <div className="MainWrapper">
        <div className="AnimatedCircle">
          <span className="Circles1"></span>
          <span className="Circles2"></span>
          <span className="Circles3"></span>
        </div>
        <h1>Weather App</h1>
        <form className="form" onSubmit={GetCity}>
         <ThemeProvider theme={theme}>
          <TextField size="small" className="form-control" type="text" value={City} placeholder="Search..." onChange={(e)=>setCity(e.target.value)}/>
        </ThemeProvider>
          <IconButton variant="contained" color="secondary" type="submit"><SearchIcon/></IconButton>
        </form>
     {(!Res||Res.cod!==200)?(<p className="Notfound">{Res.message}</p>):(
        <div className="dataWrapper">
          <div className="cityWrapper">
            <h1><PinDropIcon/>{Res.name}</h1>
          </div>
          
          <div className="data">
            <span className="main_temp">{Res.main.temp}C°</span>
            <div className="Temps">
            <span>Max:{Res.main.temp_max}C°  ||   Min:{Res.main.temp_min}C°</span>
            <span>Weather:{Res.weather[0].description}</span>
            <hr/>
            <h3>Wind</h3>
            <span>Degree:{Res.wind.deg}deg° || Speed:{Res.wind.speed}kmph</span>
            </div>
          </div>
        </div>
       )}
      </div>
    </>
    )
}
export default WeatherApp;