import React,{useState} from "react";
import {IconButton,Button,Paper,TextField} from "@material-ui/core"
import {createMuiTheme,ThemeProvider} from "@material-ui/core/styles/"
import AddBoxIcon from '@material-ui/icons/AddBox'; 
import ClearAllIcon from '@material-ui/icons/ClearAll';
import CreateIcon from '@material-ui/icons/Create';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import TodoIcon from "./Todo.svg";
import "./todo.css";
import TodoItems from "./Items";

const Todo=()=>{
  const[inputVal,setInputVal]=useState("");
  const[Items,setItems]=useState([]);
  const[btnState,setbtnState]=useState(true);
  const[isSet,setIsSet]=useState(null);
  
  
  const Done=(id)=>{
     setItems(Items.map((x)=>{
       
       if(x.id===id){
         if(!x.complete){
           return{...x,complete:true}
         }
         else{
           return{...x,complete:false}
         }
       }
       return x;
       
     })
   )
  }
  const addItem=()=>{
    if(!inputVal){
      alert("plese enter something");
    }
    else if(inputVal && !btnState){
      setItems(Items.map((elem)=>{
       if(elem.id===isSet){
          return {...elem,name:inputVal}
        }
       return elem;
      }))
      setInputVal("")
      setbtnState(true)
      setIsSet(null)
    }
    else{
    setItems((prev)=>{
      let Val={id:new Date().getTime().toString(),name:inputVal,complete:false};
      return [...prev,Val];
    })
    setInputVal("")
    }
  }
  
  const editItem=(id)=>{
    let editVal=Items.find((elem)=>{
      return elem.id===id;
    })
    setbtnState(false);
    setInputVal(editVal.name);
    setIsSet(id)
  }
  
  const deleteItem=(id)=>{
    setItems(Items.filter((elem)=>{
      return elem.id!==id
    }))
  }
  
const RemoveAll=()=>{
      setInputVal("")
      setbtnState(true)
      setIsSet(null)
      return setItems([]);
  }
  
  
  const theme = createMuiTheme({
    overrides: {
      MuiPaper:{
        root:{
          padding:"10px",
          textAlign:"center",
          backgroundColor:"rgb(96,1,255)",
          color:"rgb(255,255,255)",
        }
      },
      MuiIconButton:{
        colorPrimary:{
          color:"#0bea49",
        },
        colorSecondary:{
          color:"#ff0000",
        }
      },
      MuiSvgIcon:{
        root:{
          width:"2rem",
          height:'2rem',
          fontSize:"2rem",
        },
        colorSecondary:{
          color:"#fff200",
          }, 
      },
      MuiButton:{
         containedPrimary:{
           backgroundColor:"rgb(0,0,0)",
          color:"rgb(255,255,255)",
        } 
      },
      MuiInput:{
        root:{
          fontWeight:"bolder",
          fontSize:"1.5rem",
          color:"#ffffff",
          padding:"0 20px",
        },
      }
    }
  })
  
  
  
  
  
  return (
    <>
      <ThemeProvider theme={theme}>
      <div className="main-div">
        <Paper className="child-div">
          <figure>
            <img src={TodoIcon} alt="Todo Logo"/>
            <figcaption>Todo App</figcaption>
          </figure>
          <div>
            <TextField label=<BorderColorIcon color="secondary"/> type="text"
              color="secondary" placeholder="Write Here..." value={inputVal} onChange={(e)=>setInputVal(e.target.value)}/>
            {btnState?<IconButton color="primary" className=" add-items" onClick={addItem}><AddBoxIcon/></IconButton>:<IconButton color="primary" className=" fa-edit" onClick={addItem}><CreateIcon/></IconButton>}
          </div>
          <div className="showItems">
            {Items.map((elem)=>{
              return <TodoItems key={elem.id} elem={elem} Done={Done} editItem={editItem} deleteItem={deleteItem}/>
            })}
          </div>
          <div className="btn">
              <Button className="effect04" color="primary" variant="contained" onClick={RemoveAll} data-sm-link-text="Clear All">
              <span><ClearAllIcon/></span>
              </Button>
          </div>
          
        </Paper>
      </div>
    </ThemeProvider>
    </>
  );
}

export default Todo;
